.heading {
    padding-top: 40px;
    background-image: url(./../../assets/images/banner-photo.jpg);
    background-color: #0e2545;
    background-repeat: no-repeat;
    background-size: cover;
    background-origin: content-box;
    background-position: center;
    text-align: center;
}
.headingContainer {
    composes: container from './../../stylesheet/main.css';
}

.headingTitle {
    composes: heading1 from './../../stylesheet/basicStyles.css';
    text-transform: uppercase;
}

.paragraph {
    composes: paragraph from './../../stylesheet/basicStyles.css';
}

.paragraph, .headingTitle {
    color: var(--bg-color);
    margin: 0 0 20px;
}

@media (min-resolution: 192dpi) {
    .heading {
        background-image: url(./../../assets/images/banner-photo-2x.jpg);
    }
}

@media screen and (min-width: 768px) {
    .heading {
        padding-top: 60px;
        text-align: left;
    }
    .headingContainer {
        padding-top: 60px;
        padding-bottom: 66px;
    }
    .headingContainer button {
        margin: 0;
    }
    .headingTitle {
        max-width: 436px;
    }
    .paragraph {
        max-width: 460px;
    }
    .headingTitle {
        margin-bottom: 26px;
    }
    .paragraph {
        margin-bottom: 34px;
    }
    
}
@media screen and (min-width: 1024px) {
    .headingContainer {
        padding-top: 108px;
        padding-bottom: 114px;
    }
    .headingTitle {
        max-width: 520px;
        margin-bottom: 24px;
    }
    .paragraph {
        max-width: 530px;
    }
}