.nav {
    width: 80vw;
    position: absolute;
    height: 100%;
    /* left: -100%; */
}
.navItem {
    display: block;
    text-decoration: none;
    margin-bottom: 26px;
    padding-left: 15px;
    color: #000;
    font-family: 'Open Sans', sans-serif;
    font-size: 16px;
}
.navItem:nth-child(5n) {
    border-bottom: black 1px solid;
    padding-bottom: 28px;
}
.navItem:last-child {
    margin-bottom: 0;
}