.button {
    background-color: var(--primary-color);
    outline: none;
    border: none;
    border-radius: 4px;
    color: #fefefe;
    font-family: 'Open Sans', sans-serif;
    font-size: 16px;
    line-height: 24px;
    font-weight: 400;
    width: 210px;
    min-height: 38px;
    display: block;
    margin: inherit;
}
.button:hover, .button:focus {
    background-color:  #d24335;
}
