/* make a custom input type file  */
.inputfile {
    width: 0.1px;
    height: 0.1px;
    opacity: 0;
    overflow: hidden;
    position: absolute;
    z-index: -1;
}

.customFile{
    display: flex;
    justify-content: space-between;
    border-radius: 4px;
    border: 1px solid #b2b9c0;
    cursor: pointer;
    overflow: hidden;
}
.placeholder {
    padding: 12px;
    border-right: 1px solid #b2b9c0;
    width: 100%;
    color: #b2b9c0;
    width: 100%;
}
.filename {
    composes: placeholder;
    color: #404040;
}
.browseButton {
    padding: 12px;
    background-color: #f8f7f5;
}
.customFile:focus {
    border-color: rgb(128, 189, 255);
    box-shadow: 0px 0px 2px 2px rgba(128, 189, 255, 0.5);
    outline: none;
}
