.header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 999;
  background-color: var(--bg-color);
}

.headerContainer {
  composes: container from './../../stylesheet/main.css';
  padding-top: 10px;
  padding-bottom: 10px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.burgerMenu {
  display: flex;
  align-items: center;
  border: none;
  font-size: 0;
  background-color: inherit;
}

@media screen and (min-width: 768px) {
  .headerContainer{
    padding-top: 18px;
    padding-bottom: 18px;
  }
}